import wx from 'weixin-js-sdk'
import {getApi} from '../../api/getApi.js'

export function wxInit (val) {
  let links = `${window.location.href}`
  let title = val.title
  let desc = val.summary
  let imgUrl = val.thumb
  wx.ready(function () {
    wx.onMenuShareTimeline({
      title: title, // 
      link: links, // JS
      imgUrl: imgUrl, // 
      success: function () {
      // 
      }
    })
    wx.onMenuShareAppMessage({
      title: title, // 
      desc: desc, // 
      link: links, // JS
      imgUrl: imgUrl, // 
      success: function () {
      // 
      }
    })
    wx.onMenuShareQQ({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      // 
      },
      cancel: function () {
      // 
      }
    })
    wx.onMenuShareQZone({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
    wx.onMenuShareWeibo({
      title: title, // 
      desc: desc, // 
      link: links, // 
      imgUrl: imgUrl, // 
      success: function () {
      },
      cancel: function () {
      }
    })
  })
  wx.error(function (res) {
    // configerrorconfigdebugresSPA
  })
}

export function share (val) {
  let url = encodeURIComponent(`${window.location.href}`)
  getApi(`/sign?share_url=${url}`).then(res => {
    if (res.data.errorCode === 0) {
      wx.config({
        debug: false,
        appId: 'wxb5b03bf29736518a',
        timestamp: res.data.data.signPackage.timestamp,
        nonceStr: res.data.data.signPackage.nonceStr,
        signature: res.data.data.signPackage.signature,
        jsApiList: [
          'onMenuShareTimeline', 'onMenuShareAppMessage', 'onMenuShareQQ', 'onMenuShareQZone', 'onMenuShareWeibo'
        ]
      })
      wxInit(val)
    }
  })
}



